import axios from "axios";
import React, { Component } from "react";
import { BiRightArrowAlt } from "react-icons/bi";
import {
  MdDelete,
  MdEdit,
  MdHighlightOff,
  MdRemoveRedEye,
  MdSettings,
} from "react-icons/md";
import { SiAddthis } from "react-icons/si";
import { TiInputChecked } from "react-icons/ti";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { PushPopupAlert } from "../../actions";
import { customizeUserAccess } from "../../actions/users";
import { PopupAlertData } from "../../components/Alert/Popup";
import Table from "../../components/Table/Table";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import {
  UserInterface,
  AccessTaskInterface,
  CustomeAccessInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";

interface AppProps {
  userInfo: UserInterface | undefined;
  manageUserId: string;
  setUserAccess: (value: AccessTaskInterface[] | null) => void;
  userAccess: AccessTaskInterface[] | null;
  setCurrentAccess: (value: AccessTaskInterface | null) => void;
  currentAccess: AccessTaskInterface | null;
  currentPage: number;

  // REDUX ==============
  PushPopupAlert: (alert: PopupAlertData) => void;
  customizeUserAccess: Function;
}

interface AppStates {
  loading: boolean;
  loadindRequest: boolean;
  // currentAccess:AccessTaskInterface | null;
  // userAccess:AccessTaskInterface[] | null;
  createPermission: boolean;
  viewPermission: boolean;
  updatePermission: boolean;
  deletePermission: boolean;
}

class _UserAccess extends Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadindRequest: false,
      // currentAccess:null,
      // userAccess:null,
      createPermission: false,
      viewPermission: false,
      updatePermission: false,
      deletePermission: false,
    };
  }

  setViewPermission = (value: boolean) => {
    this.setState({ viewPermission: value });
  };

  setLoadindRequest = (value: boolean) => {
    this.setState({ loadindRequest: value });
  };

  setCreatePermission = (value: boolean) => {
    this.setState({ createPermission: value });
  };

  setUpdatePermission = (value: boolean) => {
    this.setState({ updatePermission: value });
  };

  setDeletePermission = (value: boolean) => {
    this.setState({ deletePermission: value });
  };

  setLoadind = (value: boolean) => {
    this.setState({ loading: value });
  };

  overrideUserAccess = (
    default_access: AccessTaskInterface[],
    custome_access: AccessTaskInterface[]
  ) => {
    if (default_access && custome_access && custome_access.length > 0) {
      let overideAccess: AccessTaskInterface[] = [];
      for (const key in default_access) {
        if (Object.prototype.hasOwnProperty.call(default_access, key)) {
          const findCustom = custome_access.find(
            (custom) => custom.id === default_access[key].id
          );
          if (findCustom) {
            overideAccess.push(findCustom);
          } else {
            overideAccess.push(default_access[key]);
          }
        }
      }
      this.props.setUserAccess(overideAccess);
    }
  };

  componentDidMount = () => {
    if (this.props.userInfo && this.props.userInfo.custome_access) {
      this.props.userInfo.default_access &&
        this.overrideUserAccess(
          this.props.userInfo.default_access.access,
          this.props.userInfo.custome_access.access
        );
      this.setLoadind(false);
    } else {
      this.props.userInfo &&
        this.props.userInfo.default_access &&
        this.props.setUserAccess(this.props.userInfo.default_access.access);
      this.setLoadind(false);
    }
  };

  tableData = (): JSX.Element[] => {
    if (!this.props.userAccess)
      return [
        <tr key={Math.random()}>
          <td>No data</td>
        </tr>,
      ];
    return this.props.userAccess.map((access: AccessTaskInterface, i) => (
      <tr
        key={i + access.id + Math.random()}
        className={`text-left border-b border-gray-200 cursor-pointer hover:bg-gray-100 ${
          this.props.currentAccess &&
          this.props.currentAccess.id === access.id &&
          "bg-gray-200 border my-3 hover:bg-gray-100"
        }`}
        onClick={() =>
          this.props.currentAccess && this.props.currentAccess.id === access.id
            ? (this.props.setCurrentAccess(null),
              this.setCreatePermission(false),
              this.setViewPermission(false),
              this.setUpdatePermission(false),
              this.setDeletePermission(false))
            : (this.props.setCurrentAccess(access),
              this.setCreatePermission(access.permitted.create),
              this.setViewPermission(access.permitted.view),
              this.setUpdatePermission(access.permitted.update),
              this.setDeletePermission(access.permitted.delete))
        }
      >
        <td className="px-2 py-1">
          <label className="inline-flex items-center">
            {/* <input
              type="radio"
              name="taskAccess"
              value={access.id}
              className="form-radio h-4 w-4 text-gray-600"
              onChange={() => {
                currentAccess
                  ? this.setState({ currentAccess: null })
                  : this.setState({ currentAccess: access });
              }}
              checked={currentAccess ? true : false}
            /> */}
            <span className="text-gray-900 capitalize text-xl font-normal cursor-pointer">
              {access.task}
            </span>
          </label>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.create ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.create ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.create ? " Yes" : "No"}
          </div>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.view ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.view ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.view ? " Yes" : "No"}
          </div>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.update ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.update ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.update ? " Yes" : "No"}
          </div>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.delete ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.delete ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.delete ? " Yes" : "No"}
          </div>
        </td>
      </tr>
    ));
  };

  handleCustomization = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (this.props.userInfo) {
      this.setLoadindRequest(true);
      let task: [] | CustomeAccessInterface | null = null;
      task = this.props.userInfo.custome_access;
      let index = -1;

      try {
        if (task && this.props.currentAccess) {
          index = task.access.findIndex(
            (access) => access.id === this.props.currentAccess!.id
          );
          if (index >= 0) {
            task.access[index].permitted.create = this.state.createPermission;
            task.access[index].permitted.view = this.state.viewPermission;
            task.access[index].permitted.update = this.state.updatePermission;
            task.access[index].permitted.delete = this.state.deletePermission;
          } else {
            task.access.push({
              id: this.props.currentAccess.id,
              task: this.props.currentAccess.task,
              key: this.props.currentAccess.key,
              permitted: {
                create: this.state.createPermission,
                view: this.state.viewPermission,
                update: this.state.updatePermission,
                delete: this.state.deletePermission,
              },
            });
          }
        } else if (this.props.currentAccess) {
          task = {
            access: [
              {
                id: this.props.currentAccess.id,
                task: this.props.currentAccess.task,
                key: this.props.currentAccess.key,
                permitted: {
                  create: this.state.createPermission,
                  view: this.state.viewPermission,
                  update: this.state.updatePermission,
                  delete: this.state.deletePermission,
                },
              },
            ],
            user_id: this.props.userInfo.user_id,
            custome_user_access_id: "",
          };
        }

        if (task) {
          let res;
          if (this.props.userInfo.custome_access !== null) {
            res = await axios.patch<{ data: CustomeAccessInterface }>(
              `${API.USER}/access/custom`,
              {
                access: task.access,
                user_id: this.props.userInfo.user_id,
              }
            );
          } else {
            res = await axios.post<{ data: CustomeAccessInterface }>(
              `${API.USER}/access/custom`,
              {
                access: task.access,
                user_id: this.props.userInfo.user_id,
              }
            );
          }

          if (res) {
            task.custome_user_access_id = res.data.data.custome_user_access_id;

            this.props.customizeUserAccess(task, this.props.currentPage);
            task &&
              this.props.userInfo.default_access &&
              this.overrideUserAccess(
                this.props.userInfo.default_access.access,
                task.access
              );
            this.props.PushPopupAlert({
              title: `Resetting User Access complete successfully`,
              theme: PopupAlertTheme.info,
            });

            task = null;
            this.props.setCurrentAccess(null);
            this.setCreatePermission(false);
            this.setViewPermission(false);
            this.setUpdatePermission(false);
            this.setDeletePermission(false);
          }
        }
      } catch (error) {
        console.error({ customize_access: error });
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      this.setLoadindRequest(false);
    }
  };

  render() {
    if (this.props.userInfo === undefined) {
      return <Redirect to="/users" />;
    }

    if (this.state.loading || this.props.userInfo === null) {
      return (
        <div className="text-center py-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading User Access...
        </div>
      );
    }

    return (
      <div>
        <div className="grid grid-cols-1 md:grid-cols-5 gap-4 mx-1 my-2">
          <div
            className={`col-span-1 md:col-span-3 bg-white w-full overflow-x-auto border rounded text-center p-2 ${
              this.props.currentAccess ? "hidden md:block" : ""
            }`}
          >
            <span className="text-gray-600 mt-2 font-semibold text-2xl">
              Settings
            </span>
            {!this.state.loading && (
              <div className="overflow-x-auto w-full">
                <Table
                  columns={[
                    {
                      columnTitle: "Access Name",
                      alignText: "left",
                      padding: "p-3",
                    },
                    { columnTitle: "Create", alignText: "left" },
                    { columnTitle: "View", alignText: "left" },
                    { columnTitle: "Update", alignText: "left" },
                    { columnTitle: "Delete", alignText: "left" },
                  ]}
                  rows={this.tableData}
                  pagination={false}
                  search={true}
                  searchBG="bg-gray-50"
                  thColor="gray"
                />
              </div>
            )}
          </div>
          <div className={`col-span-1 md:col-span-2`}>
            {!this.props.currentAccess ? (
              <div className="w-full hidden md:flex flex-col items-center text-gray-600 gap-1 py-3">
                <MdSettings className="w-20 h-20" />
                <span className="text-8 md:text-12">
                  Choose Access to Customize
                </span>
              </div>
            ) : (
              <div className={``}>
                <h1 className="text-2xl md:text-3xl font-semibold text-center my-2 text-gray-900">
                  Customize User Access
                </h1>
                <div className="flex justify-center mb-3">
                  <h2 className="text-2xl font-semibold text-center text-gray-600 flex flex-row items-center">
                    <BiRightArrowAlt className="w-7 h-7" />
                    {this.props.currentAccess.task}
                  </h2>
                </div>

                <div className="flex flex-col px-2 md:px-1">
                  <form
                    className="flex flex-col items-center"
                    onSubmit={(e) => this.handleCustomization(e)}
                  >
                    <div className="inline-flex gap-2 items-center">
                      <SiAddthis className="w-7 h-7 text-gray-600" />
                      <div className="flex flex-col">
                        <h3 className="font-semibold text-xl">Create</h3>
                        <span className="text-gray-600 text-sm md:pr-1">
                          This permission gives the user the ability to create
                        </span>
                      </div>
                      <input
                        type="checkbox"
                        className="form-checkbox h-7 w-7 text-teal-600"
                        onChange={() =>
                          this.setCreatePermission(!this.state.createPermission)
                        }
                        checked={this.state.createPermission}
                        disabled={this.state.loadindRequest}
                      />
                    </div>
                    <div className="inline-flex gap-2 items-center">
                      <MdRemoveRedEye className="w-7 h-7 text-gray-600" />
                      <div className="flex flex-col">
                        <h3 className="font-semibold text-xl">View</h3>
                        <span className="text-gray-600 text-sm md:pr-4">
                          This permission gives the user the ability to view
                        </span>
                      </div>
                      <input
                        type="checkbox"
                        className="form-checkbox h-7 w-7 text-teal-600"
                        onChange={() =>
                          this.setViewPermission(!this.state.viewPermission)
                        }
                        checked={this.state.viewPermission}
                        disabled={this.state.loadindRequest}
                      />
                    </div>
                    <div className="inline-flex gap-2 items-center">
                      <MdEdit className="w-7 h-7 text-gray-600" />
                      <div className="flex flex-col">
                        <h3 className="font-semibold text-xl">Update</h3>
                        <span className="text-gray-600 text-sm">
                          This permission gives the user the ability to update
                        </span>
                      </div>
                      <input
                        type="checkbox"
                        className="form-checkbox h-7 w-7 text-teal-600"
                        onChange={() =>
                          this.setUpdatePermission(!this.state.updatePermission)
                        }
                        checked={this.state.updatePermission}
                        disabled={this.state.loadindRequest}
                      />
                    </div>
                    <div className="inline-flex gap-2 items-center">
                      <MdDelete className="w-7 h-7 text-gray-600" />
                      <div className="flex flex-col">
                        <h3 className="font-semibold text-xl">Delete</h3>
                        <span className="text-gray-600 text-sm md:pr-1">
                          This permission gives the user the ability to delete
                        </span>
                      </div>
                      <input
                        type="checkbox"
                        className="form-checkbox h-7 w-7 text-teal-600"
                        onChange={() =>
                          this.setDeletePermission(!this.state.deletePermission)
                        }
                        checked={this.state.deletePermission}
                        disabled={this.state.loadindRequest}
                      />
                    </div>

                    <div className="flex items-center justify-center w-full py-3 gap-2">
                      {/* <button className="btn btn-secondary bg-red-600">Back</button> */}
                      <button
                        className="btn bg-gray-500 hover:bg-gray-700 focus:outline-none text-white"
                        disabled={this.state.loadindRequest}
                        onClick={() => {
                          this.props.setCurrentAccess(null);
                          this.setCreatePermission(false);
                          this.setViewPermission(false);
                          this.setUpdatePermission(false);
                          this.setDeletePermission(false);
                        }}
                      >
                        Cancel
                      </button>
                      <button
                        className="btn btn-blue"
                        disabled={this.state.loadindRequest}
                      >
                        {!this.state.loadindRequest
                          ? "Save Change"
                          : "Processing.."}
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export const UserAccess = React.memo(
  connect(null, {
    customizeUserAccess,
    PushPopupAlert,
  })(_UserAccess)
);
