import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import {
  AssignRoleToUserActionInterface,
  CustomeAccessInterface,
  UserInterface,
} from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

export const userPageLimit: number = 30;

/**
 * * ****************************** INTERFACES *****************************
 */

export interface FetchUsersAction {
  type: ActionTypes.FETCH_USERS_ACCESS;
  payload: UserAccessInterface;
}

export interface BlockUserToSystem {
  type: ActionTypes.BLOCK_USER_TO_SYSTEM | ActionTypes.UNBLOCK_USER_TO_SYSTEM;
  payload: { id: string; page: number };
}

export interface AssignRoleToUserAction {
  type: ActionTypes.ASSIGN_ROLE_TO_USER;
  payload: { data: AssignRoleToUserActionInterface; page: number };
}

export interface CustomizeUserAccessAction {
  type: ActionTypes.CUSTOMIZE_USER_ACCESS;
  payload: { data: CustomeAccessInterface; page: number };
}

export interface ResetUserAccessAction {
  type: ActionTypes.RESET_USER_ACCESS;
  payload: { user_id: string; page: number };
}

export type fetchUserAccessInterface = {
  users: UserInterface[] | null;
  next: {
    page: number;
    limit: number;
  } | null;
  previous: {
    page: number;
    limit: number;
  } | null;
  total_users: number;
};

export type UserAccessInterface = {
  users: { data: UserInterface[]; page: number }[] | null;
  next: {
    page: number;
    limit: number;
  } | null;
  previous: {
    page: number;
    limit: number;
  } | null;
  total_users: number;
};

export enum documentValidationStatus {
  waiting = "waiting",
  approved = "approved",
  rejected = "rejected",
}

export interface VerifyDocumentInterface {
  user_id: string;
  doc_id: string;
  validation_status: "waiting" | "approved" | "rejected";
  validated_by: {
    user_id: string;
    comment: string;
    validation_status: "waiting" | "approved" | "rejected";
    date?: string;
  };
  in_education: boolean;
  disability: boolean;
  expiration_date: string;
}

export type AccessUserActionType =
  | FetchUsersAction
  | BlockUserToSystem
  | AssignRoleToUserAction
  | CustomizeUserAccessAction
  | ResetUserAccessAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export type FT_FetchUserAccess = (
  page: number,
  callBack?: (
    status: boolean,
    msg: string,
    data: UserInterface[] | null
  ) => void
) => void;
export const fetchUserAccess = (
  page: number,
  callback?: (
    status: boolean,
    msg: string,
    data: UserInterface[] | null
  ) => void
) => {
  callback && callback(true, "", null);
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<fetchUserAccessInterface>(
        `${API.USER}/?page=${page}&limit=${userPageLimit}`
      );
      console.log("====================================");
      console.log({ fetchUserAccess: res });
      console.log("====================================");
      dispatch<FetchUsersAction>({
        type: ActionTypes.FETCH_USERS_ACCESS,
        payload: {
          users: [{ data: res.data.users!, page: page }],
          next: res.data.next,
          previous: res.data.previous,
          total_users: res.data.total_users,
        },
      });
      callback && callback(false, "", res.data.users);
    } catch (error) {
      console.error({ user_error: error });
      callback && callback(false, errorToText(error), null);
    }
  };
};

export const blockUserToSystem = (user_id: string, page: number) => {
  return async (dispatch: Dispatch) => {
    dispatch<BlockUserToSystem>({
      type: ActionTypes.BLOCK_USER_TO_SYSTEM,
      payload: { id: user_id, page },
    });
  };
};

export const unBlockUserToSystem = (user_id: string, page: number) => {
  return async (dispatch: Dispatch) => {
    dispatch<BlockUserToSystem>({
      type: ActionTypes.UNBLOCK_USER_TO_SYSTEM,
      payload: { id: user_id, page },
    });
  };
};

export const assignRoleToUserAccess = (
  data: AssignRoleToUserActionInterface,
  page: number
) => {
  return async (dispatch: Dispatch) => {
    dispatch<AssignRoleToUserAction>({
      type: ActionTypes.ASSIGN_ROLE_TO_USER,
      payload: { data, page },
    });
  };
};

export const customizeUserAccess = (
  data: CustomeAccessInterface,
  page: number
) => {
  return async (dispatch: Dispatch) => {
    dispatch<CustomizeUserAccessAction>({
      type: ActionTypes.CUSTOMIZE_USER_ACCESS,
      payload: { data, page },
    });
  };
};

export const resetUserAccess = (data: string, page: number) => {
  return async (dispatch: Dispatch) => {
    dispatch<ResetUserAccessAction>({
      type: ActionTypes.RESET_USER_ACCESS,
      payload: { user_id: data, page },
    });
  };
};

export const FC_GetUserBySearch = async (
  search_value: string,
  callback: (
    status: boolean,
    msg: string,
    data: UserInterface | null | ""
  ) => void
) => {
  callback(true, "", null);
  try {
    setAxiosToken();
    const res = await axios.get<UserInterface | null | "">(
      `${API.USER}/check/${search_value}`
    );
    console.log("=================FC_GetUserBySearch===================");
    console.log({ FC_GetUserBySearch: res });
    console.log("=================FC_GetUserBySearch===================");
    callback(false, "", res.data);
  } catch (error) {
    console.error({ user_error: error });
    callback(false, errorToText(error), null);
  }
};
