export const getUserById = (users: UserInterface[], userId: string) => {
  return users.find((item) => item.user_id === userId);
};

export enum ModalState {
  View_User = "View User Info",
  Add_Access = "Add Access To User",
  Update_Access = "Update User Access",
  Close_Modal = "Close Modal",
}

// Table replaced with Emmy's function
export const LocationsInArray = (
  LocationData: RoomLocationInterface[]
): LocationRoomTableInterface[] | [] => {
  let locationTemp: LocationRoomTableInterface[] = [];
  for (let p in LocationData) {
    let districts = LocationData[p].districts;
    for (let d in districts) {
      let centers = districts[d].centers;
      for (let c in centers) {
        let rooms = centers[c].rooms;
        for (let r in rooms) {
          locationTemp.push({
            province_id: LocationData[p]._id,
            province_name: LocationData[p].province_name,
            district_id: districts[d]._id,
            district_name: districts[d].district_name,
            center_id: centers[c]._id,
            center_name: centers[c].center_name,
            room_id: rooms[r]._id,
            room_name: rooms[r].room_number.toString(),
            capacity: rooms[r].capacity,
            occupied: rooms[r].occupied,
          });
        }
      }
    }
  }
  return locationTemp;
};

interface AppProps {
  users: UserInterface[];
  roleList: SystemUserRoleInterface[];
  userTypes: "all" | "active" | "blocked";
  loadingUser: boolean;
  schoolLevels: schoolLevelsStoreInterface;
  setLoadingUser: (value: boolean) => void;
  manageUser: UserInterface | null;
  setManageUser: (value: UserInterface | null) => void;
  changeRole: boolean;
  setChangeRole: (value: boolean) => void;
  currentPage: number;
  manageUserId: string;
  setManageUserId: (value: string) => void;
  searchFind: string;

  // REDUX =================
  allRoom: RoomLocationInterface[];
  getAllRoom: Function;
  blockUserToSystem: Function;
  unBlockUserToSystem: Function;
  resetUserAccess: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
  auth: Auth;
}

interface AppStates {
  loadingRequest: boolean;
  loading: boolean;
  loadingRequestType: "RESET" | "STATUS" | "CHANGE_ROLE" | null;
  roomList: RoomLocationInterface[] | null;
  roomTable: LocationRoomTableInterface[] | null;
  userAccess: AccessTaskInterface[] | null;
  currentAccess: AccessTaskInterface | null;
}

class UserList extends Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loadingRequest: false,
      loading: true,
      loadingRequestType: null,
      roomList: null,
      roomTable: null,
      userAccess: null,
      currentAccess: null,
    };
  }

  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingRequestType = (
    value: "RESET" | "STATUS" | "CHANGE_ROLE" | null
  ) => {
    this.setState({ loadingRequestType: value });
  };

  setRoomList = (value: RoomLocationInterface[] | null) => {
    this.setState({ roomList: value });
  };

  setRoomTables = (value: LocationRoomTableInterface[] | null) => {
    this.setState({ roomTable: value });
  };

  setCurrentAccess = (value: AccessTaskInterface | null) => {
    this.setState({ currentAccess: value });
  };

  setUserAccess = (value: AccessTaskInterface[] | null) => {
    this.setState({ userAccess: value });
  };

  componentDidMount = () => {
    if (
      this.props.allRoom.length === 0 &&
      FC_HasInstitute(this.props.auth.education_institution_id) === true
    ) {
      this.props.getAllRoom(
        this.props.auth.education_institution_id!,
        (status: boolean, msg: string, data?: RoomLocationInterface[]) => {
          this.setLoading(status);
          this.props.setLoadingUser(status);
          if (!status && msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          if (data && data.length > 0) {
            this.setRoomList(data);
            this.setRoomTables(LocationsInArray(data));
          }
        }
      );
    } else {
      this.setLoading(false);
    }
    // else if (this.state.loading) {
    //   this.setRoomList(this.props.allRoom);
    //   this.setRoomTables(LocationsInArray(this.props.allRoom));
    //   this.props.setLoadingUser(false);
    //   this.setLoading(false);
    // }
  };

  tableData = (): JSX.Element[] => {
    if (this.props.loadingUser || this.state.loading) {
      return [
        <tr
          key={Math.random()}
          className="border-b border-gray-200 hover:bg-gray-100"
        >
          <td className="p-3 text-center" colSpan={8}>
            <Loading />
          </td>
        </tr>,
      ];
    }

    let data: UserInterface[] = this.props.users;
    if (data.length <= 0) {
      return [
        <tr
          key={Math.random()}
          className="border-b border-gray-200 hover:bg-gray-100"
        >
          <td className="p-3 text-center" colSpan={8}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    const pageNumber: number =
      this.props.currentPage > 1 ? (this.props.currentPage - 1) * 30 : 0;

    return data.map((user: UserInterface, index: number) => (
      <tr
        key={index + Math.random()}
        className="border-b border-gray-200 hover:bg-gray-100 group cursor-pointer"
        onClick={() => {
          this.props.setManageUser(user);
          this.props.setManageUserId(user.user_id);
        }}
      >
        <td className="p-3 text-left whitespace-nowrap">
          {pageNumber + index + 1}
        </td>
        <td className="p-3 text-left">
          <span>
            {user.user_info &&
              user.user_info.fname + " " + user.user_info.lname}
          </span>
        </td>
        <td className="p-3 text-left">{user.email}</td>
        {/* <td className="p-3 text-left">{user.phone_number}</td> */}
        <td className="p-3 text-left">
          {user.user_info && user.user_info.sex === "M" ? "Male" : "Female"}
        </td>
        <td className="p-3 text-left text-blue-700 text-base font-medium">
          {user.default_access &&
            user.default_access.system_user_role_id &&
            user.default_access.system_user_role_id && (
              <span
                className={`px-2 py-1 font-semibold rounded text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100`}
              >
                {getRoleById(
                  this.props.roleList,
                  user.default_access.system_user_role_id
                )?.role &&
                  capitalizeFirstLetter(
                    getRoleById(
                      this.props.roleList,
                      user.default_access.system_user_role_id
                    )?.role
                  )}
              </span>
            )}
        </td>
        {/* {userTypes === "all" && (
          <td>
            {user.is_active ? (
              <span className={`px-2 py-1 font-semibold text-green-700`}>
                Active
              </span>
            ) : (
              <span className={`px-2 py-1 font-semibold text-red-700`}>
                Blocked
              </span>
            )}
          </td>
        )} */}
        <td className="px-3 text-center">
          <button
            className="btn bg-gray-200 group-hover:bg-blue-700 group-hover:text-white focus:outline-none"
            title="Manage User Access"
            onClick={() => {
              this.props.setManageUser(user);
              this.props.setManageUserId(user.user_id);
            }}
          >
            Manage
          </button>
        </td>
      </tr>
    ));
  };

  blockUser = async (user_id: string, actionType: boolean) => {
    if (!user_id) {
      this.props.PushPopupAlert({
        title: "User Access is required doesn't exist",
        theme: PopupAlertTheme.danger,
      });
    }

    this.setLoadingRequest(true);

    if (
      !window.confirm(
        `Are sure you want to ${actionType ? "block" : "activate"} this user?`
      )
    ) {
      return this.setLoadingRequest(false);
    }

    try {
      const res = await axios.patch(
        `${API.AUTH}/auth/user/disactive/${user_id}`,
        {
          is_active: !actionType,
        }
      );
      if (res) {
        console.log("====================================");
        console.log({ blockUserToSystem: res });
        console.log("====================================");
        actionType
          ? this.props.blockUserToSystem(user_id, this.props.currentPage)
          : this.props.unBlockUserToSystem(user_id, this.props.currentPage);

        this.props.PushPopupAlert({
          title: `User has been ${actionType ? "blocked" : "activated"}!`,
          theme: PopupAlertTheme.info,
        });
      }
    } catch (error) {
      console.error({ blockUser_ERROR: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }

    this.setLoadingRequest(false);
    user_id = "";
  };

  restUserAccess = async (user_id: string) => {
    if (!user_id) {
      this.props.PushPopupAlert({
        title: "User Access is required doesn't exist",
        theme: PopupAlertTheme.warning,
      });
    }

    if (!window.confirm(`Are sure you want to reset to default access?`)) {
      return this.setLoadingRequest(false);
    }

    this.setLoadingRequest(true);
    this.setLoadingRequestType("RESET");

    try {
      const res = await axios.delete(
        `${API.USER}/access/custom/user/${user_id}`
      );
      res && this.props.resetUserAccess(user_id, this.props.currentPage);
      let resetedAccess: UserInterface | undefined | null = isEmptyOrSpaces(
        this.props.searchFind
      )
        ? this.props.users.find((user) => user.user_id === user_id)
        : this.props.manageUser;
      if (resetedAccess) {
        resetedAccess.default_access &&
          this.setUserAccess(resetedAccess.default_access.access);
        this.props.PushPopupAlert({
          title: `Resetting User Access complete successfully`,
          theme: PopupAlertTheme.info,
        });
        this.setCurrentAccess(null);
      }
    } catch (error) {
      console.error({ reseting_user_access: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }

    this.setLoadingRequest(false);
    this.setLoadingRequestType(null);
    user_id = "";
  };

  distinctDistrict = (
    location: RoomLocationInterface[],
    province_id?: string
  ): DistrictInterface[] => {
    let ids: string[] = [],
      result: DistrictInterface[] = [];

    if (province_id) {
      result = location.find((item) => item._id === province_id)
        ? location.find((item) => item._id === province_id)!.districts
          ? location.find((item) => item._id === province_id)!.districts
          : []
        : [];
    } else {
      for (const key in location) {
        if (Object.prototype.hasOwnProperty.call(location, key)) {
          for (const keyDistrict in location[key].districts) {
            if (
              Object.prototype.hasOwnProperty.call(
                location[key].districts,
                keyDistrict
              )
            ) {
              if (!ids.includes(location[key].districts[keyDistrict]._id)) {
                ids.push(location[key].districts[keyDistrict]._id);
                result.push(location[key].districts[keyDistrict]);
              }
            }
          }
        }
      }
    }

    return result;
  };

  render() {
    const DATA_GetUser = isEmptyOrSpaces(this.props.searchFind)
      ? this.props.users.find(
          (user) => user.user_id === this.props.manageUserId
        )
      : this.props.manageUser
      ? this.props.manageUser
      : undefined;
    const DATA_GetRoleById =
      DATA_GetUser && DATA_GetUser!.default_access
        ? getRoleById(
            this.props.roleList,
            DATA_GetUser!.default_access!.system_user_role_id
          )
        : undefined;

    const DATA_ThisRoom =
      this.state.roomTable && DATA_GetUser
        ? this.state.roomTable.find(
            (room) => room.room_id === DATA_GetUser!.default_access?.room_id
          )
        : undefined;

    return (
      <div>
        {!this.props.manageUser && (
          <div className="grid md:grid-cols-5 gap-2 mb-5">
            <div className={`md:col-span-5 rounded`}>
              <div className="my-2 flex sm:flex-row flex-col justify-between">
                <div className="flex flex-col md:flex-row gap-1 items-center mx-1.5">
                  {/* <span className="text-lg">All User</span>
                  <MdChevronRight className="w-7 h-7" />
                  <span className="text-lg underline">All User</span> */}
                </div>
              </div>
              {this.props.users && (
                <Table
                  columns={[
                    { columnTitle: "#", alignText: "left", padding: "p-3" },
                    { columnTitle: "Names", alignText: "left", padding: "p-3" },
                    { columnTitle: "Email", alignText: "left", padding: "p-3" },
                    {
                      columnTitle: "Gender",
                      alignText: "left",
                      padding: "p-3",
                    },
                    { columnTitle: "Role", alignText: "left", padding: "p-3" },
                    // { columnTitle: "Status", alignText: "left", padding: "p-3" },
                    { columnTitle: "", alignText: "left", padding: "p-3" },
                  ]}
                  rows={this.tableData}
                  pagination={false}
                  search={true}
                  thColor="gray"
                  loading={this.props.loadingUser || this.state.loading}
                />
              )}
            </div>
          </div>
        )}

        {/* -------------------------------------------------------- */}

        {this.props.manageUser && (
          <div className="bg-gray-100 h-100">
            {/* header */}
            <div className="bg-white border-b border-t px-4 py-2 flex flex-col md:flex-row items-center gap-x-14">
              {DATA_GetUser ? (
                <div
                  key={DATA_GetUser!.user_id + Math.random()}
                  className="flex flex-col md:flex-row md:items-start items-center w-full gap-3"
                >
                  <img
                    src={
                      DATA_GetUser!.user_info.photo !== "pic" &&
                      DATA_GetUser!.user_info.photo !== undefined
                        ? FC_GetBase64Img(DATA_GetUser!.user_info.photo)
                        : profile
                    }
                    alt=""
                    className="rounded-full w-32 h-32 object-contain"
                  />
                  <div className="flex flex-col gap-1 items-start w-full">
                    <span className="text-2xl font-semibold">
                      {DATA_GetUser!.user_info.fname +
                        " " +
                        DATA_GetUser!.user_info.lname}
                    </span>
                    <div className="flex items-center">
                      <MdLock className="w-5 h-5" />
                      <span className="text-lg font-medium">
                        {DATA_GetRoleById && DATA_GetRoleById.role}
                      </span>
                    </div>
                    <div className="flex items-center gap-1">
                      <span className="text-gray-500">Level:</span>
                      <span className="text-base font-medium">
                        {DATA_GetRoleById && DATA_GetRoleById.room_required
                          ? "Room"
                          : DATA_GetRoleById &&
                            DATA_GetRoleById.education_institution_required
                          ? "Institute"
                          : "-"}
                      </span>
                    </div>
                    {DATA_GetRoleById &&
                    DATA_GetRoleById.education_institution_required ? (
                      <div className="flex items-center gap-1">
                        <span className="text-gray-500">Institute:</span>
                        <span className="text-base font-medium">
                          {DATA_GetUser!.default_access &&
                          this.props.schoolLevels.education_categories &&
                          getInstituteById(
                            this.props.schoolLevels.education_categories,
                            DATA_GetUser!.default_access!
                              .education_institution_id
                          )
                            ? getInstituteById(
                                this.props.schoolLevels.education_categories,
                                DATA_GetUser!.default_access!
                                  .education_institution_id
                              )!.education_category_title
                            : "Unknown Institute"}
                        </span>
                      </div>
                    ) : (
                      DATA_GetRoleById &&
                      DATA_GetRoleById.room_required && (
                        <div className="flex items-center gap-1">
                          {/* <span className="text-gray-500">Room:</span> */}
                          <MdLocationOn className="text-gray-500" />
                          {DATA_ThisRoom && (
                            <span className="text-base font-medium">
                              {DATA_ThisRoom?.district_name +
                                " - " +
                                DATA_ThisRoom?.center_name +
                                " - Room " +
                                DATA_ThisRoom?.room_name}
                            </span>
                          )}
                        </div>
                      )
                    )}
                  </div>
                </div>
              ) : (
                <div className="w-full">
                  <NoResult
                    theme={NoResultThemes.Dark}
                    value={"User info not found"}
                  />
                </div>
              )}
              {DATA_GetUser && (
                <div
                  key={DATA_GetUser!.user_id + Math.random()}
                  className="flex flex-col w-full"
                >
                  <div className="flex items-center gap-1">
                    <span className="text-gray-500">NID:</span>
                    <span className="text-base font-medium">
                      {DATA_GetUser!.n_id}
                    </span>
                  </div>
                  <div className="flex items-center gap-1">
                    <span className="text-gray-500">Email:</span>
                    <span className="text-base font-medium">
                      {DATA_GetUser!.email}
                    </span>
                  </div>
                  <div className="flex items-center gap-1">
                    <span className="text-gray-500">Phone:</span>
                    <span className="text-base font-medium">
                      {DATA_GetUser!.phone_number}
                    </span>
                  </div>
                  <div className="flex items-center gap-1">
                    <span className="text-gray-500">Status:</span>
                    <span className="text-base font-medium">
                      {DATA_GetUser!.is_active === false ? "Blocked" : "Active"}
                    </span>
                  </div>
                  <div className="flex items-center gap-1">
                    {DATA_GetUser!.custome_access && (
                      <button
                        className={`px-2 py-1 font-semibold text-gray-50 inline-flex items-center rounded ${
                          this.state.loadingRequestType &&
                          this.state.loadingRequestType === "RESET"
                            ? "cursor-wait bg-gray-400"
                            : "bg-gray-600"
                        }`}
                        onClick={() =>
                          this.restUserAccess(DATA_GetUser!.user_id)
                        }
                        disabled={this.state.loadingRequest}
                      >
                        <MdLock className="w-4 h-4 fill-current" />
                        <span>
                          {this.state.loadingRequestType &&
                          this.state.loadingRequestType === "RESET"
                            ? "Resetting..."
                            : "Reset Access"}
                        </span>
                      </button>
                    )}
                    {!this.props.changeRole && (
                      <button
                        className="bg-yellow-300 hover:bg-yellow-400 px-2 py-1 font-semibold text-black inline-flex items-center rounded"
                        onClick={() => this.props.setChangeRole(true)}
                      >
                        <MdLock className="w-4 h-4 fill-current" />
                        <span>Change Role</span>
                      </button>
                    )}
                    {DATA_GetUser!.is_active ? (
                      <button
                        className={`px-2 py-1 font-semibold text-gray-100 inline-flex items-center rounded ${
                          this.state.loadingRequestType &&
                          this.state.loadingRequestType === "STATUS"
                            ? "cursor-wait bg-red-500"
                            : "bg-red-700"
                        }`}
                        onClick={() =>
                          !this.state.loadingRequest &&
                          this.props.manageUser &&
                          this.blockUser(
                            DATA_GetUser!.user_id,
                            DATA_GetUser!.is_active
                          )
                        }
                        disabled={this.state.loadingRequest}
                      >
                        <MdLock className="w-4 h-4 fill-current" />
                        <span>
                          {this.state.loadingRequestType &&
                          this.state.loadingRequestType === "RESET"
                            ? "Loading..."
                            : "Block user"}
                        </span>
                      </button>
                    ) : (
                      <button
                        className={`px-2 py-1 font-semibold text-gray-100 inline-flex items-center rounded ${
                          this.state.loadingRequestType &&
                          this.state.loadingRequestType === "STATUS"
                            ? "cursor-wait bg-yellow-500"
                            : "bg-yellow-700"
                        }`}
                        onClick={() =>
                          !this.state.loadingRequest &&
                          this.props.manageUser &&
                          this.blockUser(
                            DATA_GetUser!.user_id,
                            DATA_GetUser!.is_active
                          )
                        }
                        disabled={this.state.loadingRequest}
                      >
                        <MdLock className="w-4 h-4 fill-current" />
                        <span>
                          {this.state.loadingRequestType &&
                          this.state.loadingRequestType === "RESET"
                            ? "Loading..."
                            : "Re-Activate"}
                        </span>
                      </button>
                    )}
                  </div>
                </div>
              )}
            </div>
            {/* end header */}

            {/* content */}
            {this.props.manageUser && this.props.changeRole ? (
              <UserRole
                userInfo={this.props.manageUser}
                roleList={this.props.roleList}
                district={
                  this.state.roomList
                    ? this.distinctDistrict(this.state.roomList)
                    : []
                }
                roomList={this.state.roomList}
                setRoomList={this.setRoomList}
                setRoomTables={this.setRoomTables}
                setChangeRole={this.props.setChangeRole}
                currentPage={this.props.currentPage}
              />
            ) : (
              <UserAccess
                userInfo={DATA_GetUser}
                manageUserId={this.props.manageUserId}
                setUserAccess={this.setUserAccess}
                userAccess={this.state.userAccess}
                setCurrentAccess={this.setCurrentAccess}
                currentAccess={this.state.currentAccess}
                currentPage={this.props.currentPage}
              />
            )}
            {/* end content */}
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = ({
  schoolLevels,
  locations,
  auth,
}: StoreState): {
  schoolLevels: schoolLevelsStoreInterface;
  allRoom: RoomLocationInterface[];
  auth: Auth;
} => {
  return {
    schoolLevels,
    allRoom: locations.roomLocation,
    auth,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    blockUserToSystem,
    unBlockUserToSystem,
    resetUserAccess,
    getAllRoom,
    PushPopupAlert,
  })(UserList)
);
