type RoleListProps = {
  setChooseRole: (value: boolean) => void;
  roleList: SystemUserRoleInterface[];
  handleSelectRole: (role_id: string, TYPE?: "SELECT" | "DEFAULT") => void;
  loadindRequest: boolean;
};

const RoleList: React.FC<RoleListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  return (
    <section className="border-2 rounded border-gray-400">
      {/* SEARCH PART */}
      <div className="flex items-center w-full my-1">
        <MdSearch className="w-6 h-6 absolute ml-2" />
        <input
          type="text"
          placeholder="Search"
          className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 focus:border-gray-700 w-full rounded"
          onChange={(e) => setSearchInput(e.target.value)}
          value={searchInput}
          disabled={props.roleList.length > 0 ? false : true}
        />
        <button
          className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
          type="button"
          onClick={() => {
            props.setChooseRole(false);
          }}
        >
          Close
        </button>
      </div>
      {/* END SEARCH PART ///////////////// */}

      <div className="w-full h-52 mt-2 overflow-y-auto divide-y divide-gray-300">
        {search(props.roleList, searchInput).length > 0 ? (
          search(props.roleList, searchInput).map((role, index) => (
            <section key={index + role.role}>
              <div className="group flex items-center justify-between my-1 hover:bg-gray-100">
                <div
                  className="p-1 w-full mr-2 cursor-pointer"
                  onClick={() => {
                    props.setChooseRole(false);
                    !props.loadindRequest &&
                      props.handleSelectRole(role.system_user_role_id);
                  }}
                >
                  <p className="text-sm font-medium text-black">
                    {capitalizeFirstLetter(role.role)}
                  </p>
                </div>
                <button
                  type="button"
                  className="bg-blue-200 group-hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
                >
                  Choose
                </button>
              </div>
            </section>
          ))
        ) : (
          <div className="text-center p-3">
            <p className="text-base font-medium text-black">
              No role available
            </p>
          </div>
        )}
      </div>
    </section>
  );
};

export default React.memo(RoleList);
