import axios from "axios";
import React, { Component } from "react";
import { MdHighlightOff } from "react-icons/md";
import { TiInputChecked } from "react-icons/ti";
import { connect } from "react-redux";
import {
  schoolLevelsStoreInterface,
  PushPopupAlert,
  Auth,
} from "../../actions";
import { getAllRoom } from "../../actions/location";
import { assignRoleToUserAccess } from "../../actions/users";
import { PopupAlertData } from "../../components/Alert/Popup";
import Loading from "../../components/Loading/Loading";
import Table from "../../components/Table/Table";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import {
  UserInterface,
  SystemUserRoleInterface,
  DistrictInterface,
  RoomLocationInterface,
  AssignRoleToUserActionInterface,
  LocationRoomTableInterface,
  RoleToUserInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import { FC_HasInstitute } from "../../utils/functions";
import RoleList from "./RoleList";
import { LocationsInArray } from "./UserList";
const url = `${API.AUTH}/auth/user/access`;

interface AppProps {
  currentPage: number;
  userInfo: UserInterface;
  roleList: SystemUserRoleInterface[];
  district: DistrictInterface[];
  assignRoleToUserAccess: Function;
  roomList: RoomLocationInterface[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  setChangeRole: (value: boolean) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
  getAllRoom: Function;
  setRoomList: (value: RoomLocationInterface[] | null) => void;
  setRoomTables: (value: LocationRoomTableInterface[] | null) => void;
  auth: Auth;
}

interface AppState {
  loading: boolean;
  loadingForm: boolean;
  loadingRequest: boolean;
  roleId: string;
  provinceId: string;
  districtId: string;
  roomId: string;
  instituteId: string;
  centerId: string;
  selectedRole: SystemUserRoleInterface | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  chooseRole: boolean;
  loadingRoom: boolean;
}

class UserRole extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadingForm: true,
      loadingRequest: false,
      roleId: "",
      provinceId: "",
      districtId: "",
      roomId: "",
      instituteId: "",
      centerId: "",
      selectedRole: null,
      formError: null,
      chooseRole: false,
      loadingRoom: false,
    };
  }

  setRoleId = (value: string) => {
    this.setState({ roleId: value });
  };

  setSelectedRole = (value: SystemUserRoleInterface | null) => {
    this.setState({ selectedRole: value });
  };

  setProvinceId = (value: string) => {
    this.setState({ provinceId: value });
  };

  setDistrictId = (value: string) => {
    this.setState({ districtId: value });
  };

  setCenterId = (value: string) => {
    this.setState({ centerId: value });
  };

  setRoomId = (value: string) => {
    this.setState({ roomId: value });
  };

  setLoadingForm = (value: boolean) => {
    this.setState({ loadingForm: value });
  };

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setInstituteId = (value: string) => {
    this.setState({ instituteId: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };
  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };
  setChooseRole = (value: boolean) => {
    this.setState({ chooseRole: value });
  };

  setDefaultState = (
    default_access: RoleToUserInterface,
    role: SystemUserRoleInterface
  ) => {
    // SET DEFAULT PROVINCE && DISTRICT ID =======================
    let LOAD_province: LocationRoomTableInterface | undefined;
    let LOAD_district: DistrictInterface | undefined;
    if (role.district_required && this.props.roomList) {
      LOAD_district = this.distinctDistrict(this.props.roomList).find(
        (item) => item._id === default_access!.district_id
      );
      LOAD_district && this.setProvinceId(LOAD_district.province_id);
      LOAD_district && this.setDistrictId(default_access!.district_id);
      this.setLoadingForm(false);
    } else if (role.room_required && this.props.roomList) {
      // SET DEFAULT PROVINCE && DISTRICT && ROOM ID =======================

      LOAD_province = LocationsInArray(this.props.roomList).find(
        (item) => item.room_id === default_access!.room_id
      );
      LOAD_province && this.setProvinceId(LOAD_province.province_id);
      LOAD_province && this.setDistrictId(LOAD_province.district_id);
      LOAD_province && this.setCenterId(LOAD_province.center_id);
      this.setRoomId(default_access.room_id);
      this.setLoadingForm(false);
    } else {
      this.setLoadingForm(false);
    }
  };

  componentDidMount = () => {
    if (this.props.roleList.length) {
      if (this.props.userInfo.default_access && this.state.loadingForm) {
        // SET DEFAULT ROLE ID =======================
        if (!this.state.roleId.length) {
          let result = this.props.roleList.find(
            (role) =>
              role.system_user_role_id ===
              this.props.userInfo.default_access!.system_user_role_id
          );
          if (result) {
            this.setRoleId(
              this.props.userInfo.default_access.system_user_role_id
            );
            this.setSelectedRole(result);
            this.setDefaultState(this.props.userInfo.default_access, result);
          }
        }

        console.log("====================================");
        console.log({ vvvvv: this.props.userInfo.default_access });
        console.log("====================================");
      }

      if (this.state.selectedRole && this.props.userInfo.default_access) {
        this.setDefaultState(
          this.props.userInfo.default_access,
          this.state.selectedRole
        );
      }

      this.setLoading(false);
    }
  };

  handleSelectRole = (
    role_id: string,
    TYPE: "SELECT" | "DEFAULT" = "SELECT"
  ) => {
    if (this.props.roleList) {
      let result = this.props.roleList.find(
        (role) => role.system_user_role_id === role_id
      );
      if (result) {
        this.setRoleId(role_id);
        this.setSelectedRole(result);
        this.state.formError &&
          this.state.formError.element === "role_id" &&
          this.setFormError(null);

        if (TYPE === "SELECT") {
          this.setProvinceId("");
          this.setDistrictId("");
          this.setCenterId("");
          this.setRoomId("");
          this.setInstituteId("");
        }
      }
    }
  };

  tableData = (): JSX.Element[] => {
    if (
      this.state.roleId.length > 0 &&
      this.state.selectedRole === null &&
      this.props.roleList
    ) {
      this.handleSelectRole(this.state.roleId, "DEFAULT");
    }
    if (this.state.selectedRole) {
      return this.state.selectedRole.access.map((access, i) => (
        <tr
          key={i + access.id + Math.random()}
          className={`text-left border-b border-gray-200 hover:bg-gray-100`}
        >
          <td className="px-2 py-1">
            <label className="inline-flex items-center">
              <span className="text-gray-900 capitalize text-xl font-normal cursor-pointer">
                {access.task}
              </span>
            </label>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.create ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.create ? (
                <TiInputChecked />
              ) : (
                <MdHighlightOff />
              )}
              {access.permitted.create ? " Yes" : "No"}
            </div>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.view ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.view ? <TiInputChecked /> : <MdHighlightOff />}
              {access.permitted.view ? " Yes" : "No"}
            </div>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.update ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.update ? (
                <TiInputChecked />
              ) : (
                <MdHighlightOff />
              )}
              {access.permitted.update ? " Yes" : "No"}
            </div>
          </td>
          <td>
            <div
              className={`inline-flex items-center text-xl ${
                access.permitted.delete ? "text-green-700" : "text-red-700"
              }`}
            >
              {access.permitted.delete ? (
                <TiInputChecked />
              ) : (
                <MdHighlightOff />
              )}
              {access.permitted.delete ? " Yes" : "No"}
            </div>
          </td>
        </tr>
      ));
    }
    const optionB = [
      <tr key="55re">
        <td colSpan={5} className="text-center px-2 py-2">
          No Default Access Available on this Role.
        </td>
      </tr>,
    ];
    return optionB;
  };

  assignRoleToUser = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!window.confirm(`Are sure you want to change user role?`)) {
      return false;
    }

    if (!this.state.selectedRole) {
      return this.setFormError({
        element: "role_id",
        msg: "User role is required!",
      });
    }

    if (
      this.state.selectedRole.room_required === true &&
      isEmptyOrSpaces(this.state.provinceId)
    ) {
      return this.setFormError({
        element: "province_id",
        msg: "Province is required!",
      });
    }

    if (
      this.state.selectedRole.education_institution_required === true &&
      isEmptyOrSpaces(this.state.instituteId)
    ) {
      return this.setFormError({
        element: "institute_id",
        msg: "Institute is required on this role!",
      });
    }

    if (
      (this.state.selectedRole.room_required === true ||
        this.state.selectedRole.district_required === true) &&
      isEmptyOrSpaces(this.state.districtId)
    ) {
      return this.setFormError({
        element: "district_id",
        msg: "District is required!",
      });
    }

    if (
      this.state.selectedRole.room_required === true &&
      isEmptyOrSpaces(this.state.centerId)
    ) {
      return this.setFormError({
        element: "center_id",
        msg: "Center is required!",
      });
    }

    if (
      this.state.selectedRole.room_required === true &&
      isEmptyOrSpaces(this.state.roomId)
    ) {
      return this.setFormError({
        element: "room_id",
        msg: "Room is required!",
      });
    }
    this.setLoadingRequest(true);
    let formData: AssignRoleToUserActionInterface = {
      user_id: this.props.userInfo.user_id,
      data: {
        system_user_role_id: this.state.roleId,
        status: 1,
        role: this.state.selectedRole.role,
        access: this.state.selectedRole.access,
        school_id: "",
        district_id: "",
        room_id: this.state.selectedRole.room_required ? this.state.roomId : "",
        education_institution_id: this.state.selectedRole
          .education_institution_required
          ? this.state.instituteId
          : "",
      },
    };

    try {
      const res = await axios.post(`${url}/addroletouser`, {
        user_id: this.props.userInfo.user_id,
        system_user_role_id: this.state.roleId,
        status: 1,
        role: this.state.selectedRole.role,
        access: true,
        school_id: "",
        district_id:
          this.state.selectedRole.district_required === true
            ? this.state.districtId
            : "",
        room_id:
          this.state.selectedRole.room_required === true
            ? this.state.roomId
            : "",
        education_institution_id: this.state.selectedRole
          .education_institution_required
          ? this.state.instituteId
          : "",
      });
      if (res) {
        console.log("====================================");
        console.log({ assignRoleToUser: res });
        console.log("====================================");
        this.props.assignRoleToUserAccess(formData, this.props.currentPage);
        this.setLoadingRequest(false);
        this.props.setChangeRole(false);
        this.props.PushPopupAlert({
          title: "Assigning Role to User completed successfully",
          theme: PopupAlertTheme.success,
        });
      }
    } catch (error) {
      console.log({ role_error: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  distinctDistrict = (
    location: RoomLocationInterface[],
    province_id?: string
  ): DistrictInterface[] => {
    let ids: string[] = [],
      result: DistrictInterface[] = [];

    if (province_id) {
      result = location.find((item) => item._id === province_id)
        ? location.find((item) => item._id === province_id)!.districts
          ? location.find((item) => item._id === province_id)!.districts
          : []
        : [];
    } else {
      for (const key in location) {
        if (Object.prototype.hasOwnProperty.call(location, key)) {
          for (const keyDistrict in location[key].districts) {
            if (
              Object.prototype.hasOwnProperty.call(
                location[key].districts,
                keyDistrict
              )
            ) {
              if (!ids.includes(location[key].districts[keyDistrict]._id)) {
                ids.push(location[key].districts[keyDistrict]._id);
                result.push(location[key].districts[keyDistrict]);
              }
            }
          }
        }
      }
    }

    return result;
  };

  distinctCenter = (
    rooms: RoomLocationInterface[],
    district_id: string
  ): LocationRoomTableInterface[] => {
    let ids: string[] = [],
      result: LocationRoomTableInterface[] = [];

    LocationsInArray(rooms).forEach((element) => {
      if (
        !ids.includes(element.center_id) &&
        element.district_id === district_id
      ) {
        ids.push(element.center_id);
        result.push(element);
      }
    });
    return result;
  };

  distinctRoom = (
    rooms: RoomLocationInterface[],
    center_id: string
  ): LocationRoomTableInterface[] => {
    let ids: string[] = [],
      result: LocationRoomTableInterface[] = [];

    LocationsInArray(rooms).forEach((element) => {
      if (!ids.includes(element.room_id) && element.center_id === center_id) {
        ids.push(element.room_id);
        result.push(element);
      }
    });
    return result;
  };

  FC_LoadRooms = (institution_id: string) => {
    this.props.getAllRoom(
      institution_id,
      (status: boolean, msg: string, data?: RoomLocationInterface[]) => {
        this.setState({ loadingRoom: status });
        if (!status && msg.length > 0) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
        if (data && data.length > 0) {
          this.props.setRoomList(data);
          this.props.setRoomTables(LocationsInArray(data));
        }
      }
    );
  };

  render() {
    if (
      this.state.loading === true ||
      this.state.loadingForm ||
      this.props.userInfo.default_access === null ||
      this.state.roleId.length === 0 ||
      this.state.selectedRole === null
    ) {
      return (
        <div className="text-center py-7 font-bold text-3xl text-gray-400 animate-pulse">
          Loading...
        </div>
      );
    }

    return (
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8 py-2 md:py-4 px-2 md:px-0">
        {/* left panel */}
        <div className="col-span-1 bg-white w-full overflow-x-auto border rounded">
          <div className="px-6 md:px-12 py-5 bg-white mx-auto rounded-2xl w-100">
            <h3 className="font-semibold text-2xl text-gray-800 mb-4">
              Change User Role
            </h3>
            {this.state.loadingForm ? (
              <Loading />
            ) : (
              <form onSubmit={(e) => this.assignRoleToUser(e)}>
                <div className="space-y-5">
                  <div className="space-y-2">
                    <label className="text-sm font-medium text-gray-700 tracking-wide">
                      User Role
                    </label>

                    {!this.state.chooseRole && (
                      <div
                        className={`w-full text-base bg-white text-grey-darker cursor-pointer border-2 px-4 py-3 rounded focus:outline-none focus:bg-gray-100 ${
                          this.state.formError &&
                          this.state.formError.element === "role_id"
                            ? "border-red-500"
                            : "border-gray-400"
                        }`}
                        onClick={() => {
                          !this.state.loadingRequest &&
                            this.setChooseRole(true);
                          !this.state.loadingRequest && this.setFormError(null);
                        }}
                      >
                        {this.state.selectedRole
                          ? this.state.selectedRole.role
                          : "Select role"}
                      </div>
                    )}

                    {this.state.chooseRole && (
                      <RoleList
                        setChooseRole={this.setChooseRole}
                        handleSelectRole={this.handleSelectRole}
                        roleList={this.props.roleList}
                        loadindRequest={
                          this.state.loadingRequest || this.state.loadingRoom
                        }
                      />
                    )}

                    {this.state.formError &&
                      this.state.formError.element === "role_id" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>

                  {this.state.selectedRole &&
                    (this.state.selectedRole.education_institution_required ||
                      this.state.selectedRole.district_required ||
                      this.state.selectedRole.room_required) && (
                      <div className="space-y-2">
                        <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                          Institute
                        </label>
                        <select
                          className={`block w-full text-base bg-white text-grey-darker border-2 rounded px-4 py-3 md:w-full focus:outline-none focus:bg-gray-100 ${
                            this.state.formError &&
                            this.state.formError.element === "institute_id"
                              ? "border-red-500"
                              : "border-gray-400"
                          }`}
                          onChange={(e) => {
                            this.setInstituteId(e.target.value);
                            this.setProvinceId("");
                            this.setDistrictId("");
                            this.setCenterId("");
                            this.setRoomId("");
                            this.state.formError &&
                              this.state.formError.element === "institute_id" &&
                              this.setFormError(null);
                            this.FC_LoadRooms(e.target.value);
                          }}
                          disabled={
                            this.state.loadingRequest ||
                            FC_HasInstitute(
                              this.props.auth.education_institution_id
                            ) === true ||
                            this.state.loadingRoom
                          }
                          value={
                            FC_HasInstitute(
                              this.props.auth.education_institution_id
                            ) === true
                              ? this.props.auth.education_institution_id!
                              : this.state.instituteId
                          }
                        >
                          <option value="">Select institute</option>
                          {this.props.schoolLevels.education_categories &&
                          this.props.schoolLevels.education_categories.length >
                            0 ? (
                            this.props.schoolLevels.education_categories.map(
                              (institute) => (
                                <option
                                  value={institute.education_institute_id}
                                  key={
                                    institute.education_institute_id +
                                    Math.random()
                                  }
                                >
                                  {institute.education_category_title}
                                </option>
                              )
                            )
                          ) : (
                            <option value="" disabled>
                              No institute available
                            </option>
                          )}
                        </select>
                        {this.state.formError &&
                          this.state.formError.element === "institute_id" && (
                            <span className="text-red-500">
                              {this.state.formError.msg}
                            </span>
                          )}
                      </div>
                    )}

                  {this.state.selectedRole &&
                    this.state.selectedRole.room_required && (
                      <div className="space-y-2">
                        <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                          Province
                        </label>
                        <select
                          className={`block w-full text-base bg-white text-grey-darker border-2 rounded px-4 py-3 md:w-full focus:outline-none focus:bg-gray-100 ${
                            this.state.formError &&
                            this.state.formError.element === "province_id"
                              ? "border-red-500"
                              : "border-gray-400"
                          }`}
                          onChange={(e) => {
                            this.setProvinceId(e.target.value);
                            this.state.formError &&
                              this.state.formError.element === "province_id" &&
                              this.setFormError(null);
                            this.setDistrictId("");
                            this.setCenterId("");
                            this.setRoomId("");
                          }}
                          value={this.state.provinceId}
                          disabled={
                            this.state.loadingRequest || this.state.loadingRoom
                          }
                        >
                          <option value="">Select province</option>
                          {this.props.roomList &&
                            this.props.roomList.map((province) => (
                              <option
                                key={province._id + Math.random()}
                                value={province._id}
                              >
                                {province.province_name}
                              </option>
                            ))}
                        </select>
                        {this.state.formError &&
                          this.state.formError.element === "province_id" && (
                            <span className="text-red-500">
                              {this.state.formError.msg}
                            </span>
                          )}
                      </div>
                    )}
                  {this.state.selectedRole &&
                    (this.state.selectedRole.district_required ||
                      this.state.selectedRole.room_required) && (
                      <div className="space-y-2">
                        <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                          District
                        </label>
                        <select
                          className={`block w-full text-base bg-white text-grey-darker border-2 rounded px-4 py-3 md:w-full focus:outline-none focus:bg-gray-100 ${
                            this.state.formError &&
                            this.state.formError.element === "district_id"
                              ? "border-red-500"
                              : "border-gray-400"
                          }`}
                          onChange={(e) => {
                            this.setDistrictId(e.target.value);
                            this.state.formError &&
                              this.state.formError.element === "district_id" &&
                              this.setFormError(null);
                            this.setCenterId("");
                            this.setRoomId("");
                          }}
                          value={this.state.districtId}
                          disabled={
                            this.state.loadingRequest || this.state.loadingRoom
                          }
                        >
                          <option value="">Select district</option>
                          {this.props.roomList &&
                          this.distinctDistrict(
                            this.props.roomList,
                            !isEmptyOrSpaces(this.state.provinceId)
                              ? this.state.provinceId
                              : undefined
                          ).length > 0 ? (
                            this.distinctDistrict(
                              this.props.roomList,
                              !isEmptyOrSpaces(this.state.provinceId)
                                ? this.state.provinceId
                                : undefined
                            ).map((district) => (
                              <option
                                key={district._id + Math.random()}
                                value={district._id}
                              >
                                {district.district_name}
                              </option>
                            ))
                          ) : (
                            <option value="" disabled>
                              No district found
                            </option>
                          )}
                        </select>
                        {this.state.formError &&
                          this.state.formError.element === "district_id" && (
                            <span className="text-red-500">
                              {this.state.formError.msg}
                            </span>
                          )}
                      </div>
                    )}

                  {this.state.selectedRole &&
                    this.state.selectedRole.room_required && (
                      <div className="space-y-2">
                        <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                          Center
                        </label>
                        <select
                          className={`block w-full text-base bg-white text-grey-darker border-2 rounded px-4 py-3 md:w-full focus:outline-none focus:bg-gray-100 ${
                            this.state.formError &&
                            this.state.formError.element === "center_id"
                              ? "border-red-500"
                              : "border-gray-400"
                          }`}
                          onChange={(e) => {
                            this.setCenterId(e.target.value);
                            this.state.formError &&
                              this.state.formError.element === "center_id" &&
                              this.setFormError(null);
                            this.setRoomId("");
                          }}
                          disabled={
                            this.state.loadingRequest || this.state.loadingRoom
                          }
                          value={this.state.centerId}
                        >
                          <option value="">Select center</option>
                          {this.state.districtId &&
                          this.state.districtId.length > 0 &&
                          this.props.roomList &&
                          this.distinctCenter(
                            this.props.roomList,
                            this.state.districtId
                          ).length > 0 ? (
                            this.distinctCenter(
                              this.props.roomList,
                              this.state.districtId
                            ).map((center, index) => (
                              <option
                                value={center.center_id}
                                key={center.center_id + index + Math.random()}
                              >
                                {center.center_name}
                              </option>
                            ))
                          ) : (
                            <option value="" disabled>
                              No center found
                            </option>
                          )}
                        </select>
                        {this.state.formError &&
                          this.state.formError.element === "center_id" && (
                            <span className="text-red-500">
                              {this.state.formError.msg}
                            </span>
                          )}
                      </div>
                    )}

                  {this.state.selectedRole &&
                    this.state.selectedRole.room_required && (
                      <div className="space-y-2">
                        <label className="mb-5 text-sm font-medium text-gray-700 tracking-wide">
                          Room
                        </label>
                        <select
                          className={`block w-full text-base bg-white text-grey-darker border-2 rounded px-4 py-3 md:w-full focus:outline-none focus:bg-gray-100 ${
                            this.state.formError &&
                            this.state.formError.element === "room_id"
                              ? "border-red-500"
                              : "border-gray-400"
                          }`}
                          onChange={(e) => {
                            this.setRoomId(e.target.value);
                            this.state.formError &&
                              this.state.formError.element === "room_id" &&
                              this.setFormError(null);
                          }}
                          disabled={
                            this.state.loadingRequest || this.state.loadingRoom
                          }
                          value={this.state.roomId}
                        >
                          <option value="">Select room</option>
                          {!isEmptyOrSpaces(this.state.centerId) &&
                          this.props.roomList &&
                          this.distinctRoom(
                            this.props.roomList,
                            this.state.centerId
                          ).length > 0 ? (
                            this.distinctRoom(
                              this.props.roomList,
                              this.state.centerId
                            ).map((room, index) => (
                              <option
                                value={room.room_id}
                                key={room.room_id + index + Math.random()}
                                className={`${
                                  room.room_name === "0" ? "hidden" : "block"
                                }`}
                              >
                                {room.room_name}
                              </option>
                            ))
                          ) : (
                            <option value="" disabled>
                              No room found
                            </option>
                          )}
                        </select>
                        {this.state.formError &&
                          this.state.formError.element === "room_id" && (
                            <span className="text-red-500">
                              {this.state.formError.msg}
                            </span>
                          )}
                      </div>
                    )}

                  <div className="flex justify-between 2xl:justify-start gap-2">
                    <button
                      className="btn bg-gray-500 text-gray-50 focus:outline-none"
                      onClick={() => {
                        this.props.setChangeRole(false);
                        this.setFormError(null);
                        this.setProvinceId("");
                        this.setDistrictId("");
                        this.setCenterId("");
                        this.setRoomId("");
                      }}
                      disabled={this.state.loadingRequest}
                    >
                      Back
                    </button>
                    <button
                      className="btn btn-blue focus:outline-none"
                      // onClick={() => assignRoleToUser()}
                      disabled={this.state.loadingRequest}
                    >
                      {this.state.loadingRequest ? "Loading..." : "Save Change"}
                    </button>
                  </div>
                </div>
              </form>
            )}
          </div>
        </div>
        {/* end left panel */}

        {/* ------------------------------------------------------------ */}

        {/* right panel */}
        <div className="cols-span-1 md:col-span-2 bg-white w-full border rounded text-center p-2 hidden md:block">
          <div className="my-4">
            <span className="text-gray-600 font-semibold text-2xl">
              {this.state.selectedRole?.role}'s Default Access
            </span>
          </div>
          {!this.state.loading && (
            <div className="overflow-x-auto w-full">
              <Table
                columns={[
                  {
                    columnTitle: "Access Name",
                    alignText: "left",
                    padding: "p-3",
                  },
                  { columnTitle: "Create", alignText: "left" },
                  { columnTitle: "View", alignText: "left" },
                  { columnTitle: "Update", alignText: "left" },
                  { columnTitle: "Delete", alignText: "left" },
                ]}
                rows={this.tableData}
                pagination={false}
                search={false}
                searchBG="bg-gray-50"
                thColor="gray"
                tableId="defaultAccess"
              />
            </div>
          )}
        </div>
        {/* end right panel */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return { auth, schoolLevels };
};

export default React.memo(
  connect(mapStateToProps, {
    assignRoleToUserAccess,
    getAllRoom,
    PushPopupAlert,
  })(UserRole)
);
